@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:400,600);
@import url(https://fonts.googleapis.com/css?family=Yesteryear&text=hi!);
.button{font-size:18px;background:none;border-radius:25px;border:1.5px solid rgba(255,255,255,0.3);padding:6px 9px;color:rgba(255,255,255,0.6);text-decoration:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;-webkit-transition:0.3s all ease;transition:0.3s all ease}.button:hover{border-color:rgba(255,255,255,0.8);color:rgba(255,255,255,0.9)}.button:active{border-color:rgba(255,255,255,0.5);background-color:rgba(255,255,255,0.7);color:rgba(0,0,0,0.7)}

.nav{position:fixed;left:0;right:0;padding:6px;z-index:100;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;background-color:rgba(255,255,255,0.85);-webkit-backdrop-filter:blur(12px);backdrop-filter:blur(12px);box-shadow:0px 0px 48px 24px rgba(0,0,0,0.3)}.nav.compressed{background-color:rgba(255,255,120,0.85)}.nav.compressed .nav-header{letter-spacing:48px;-webkit-transform:translate3d(0, -21px, 0);transform:translate3d(0, -21px, 0);font-size:0;opacity:0}.nav .nav-header{font-size:18px;letter-spacing:6px;text-transform:uppercase;-webkit-transition:all 0.3s ease;transition:all 0.3s ease}.nav .nav-items{margin-top:3px;font-size:15px;display:flex;justify-content:center}.nav .nav-items .nav-item{width:99px;padding:3px;border:1.5px solid rgba(0,0,0,0.8);color:rgba(0,0,0,0.8);text-decoration:none;cursor:default;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out}.nav .nav-items .nav-item:first-child{border-radius:6px 0px 0px 6px;border-right:none}.nav .nav-items .nav-item:last-child{border-left:none;border-radius:0px 6px 6px 0px;cursor:alias}.nav .nav-items .nav-item:hover{color:rgba(255,255,255,0.9);background-color:rgba(0,0,0,0.9)}.nav .nav-items .nav-item:active{color:rgba(255,255,255,0.3);background-color:#373737}

.header{text-align:center;color:rgba(255,255,255,0.7);text-shadow:0px 0px 48px rgba(0,0,0,0.6);-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:default;position:relative}.header .message{font-family:Yesteryear;font-size:21vh;margin:36vh auto;-webkit-animation-fill-mode:both;animation-fill-mode:both}.header .down-arrow{font-size:12vh;position:absolute;bottom:1vh;left:0;right:0;margin:auto;text-shadow:0px 0px 48px rgba(0,0,0,0.6);-webkit-transition:all 0.3s ease;transition:all 0.3s ease;cursor:default}.header .down-arrow:hover{text-shadow:0px 0px 48px rgba(255,255,255,0.6);color:#f0f0f0}.header .down-arrow:active{text-shadow:0px 0px 48px rgba(0,0,0,0.9);color:rgba(240,240,240,0.3)}

.projects .projects-header{font-size:6vmax;letter-spacing:15px;position:absolute;text-align:center;top:22vmin;left:0;right:0;margin:auto;color:rgba(255,255,255,0.8);text-shadow:0px 0px 48px rgba(0,0,0,0.9);text-transform:uppercase}.projects .project.zones{background-color:#2e5f8d}.projects .project.teams{background-color:#332577}.projects .project.tetris{background-color:#29714b}.projects .project.shipwars{background-color:#873b33}.projects .project.spark{background-color:#243b5e}.projects .project.lelivre{background-color:#286458}.projects .project.stockr{background-color:#5e5e5e}

.project{-webkit-animation-fill-mode:none !important;animation-fill-mode:none !important;padding:3vw 9vw;text-align:center;color:rgba(255,255,255,0.7)}.project .title{font-size:27px;color:white;margin:6px;text-transform:uppercase}.project .logo{border-radius:24px;-webkit-filter:drop-shadow(0px 0px 20px rgba(0,0,0,0.3));filter:drop-shadow(0px 0px 20px rgba(0,0,0,0.3));width:96px;margin:6px}.project .logo.lelivre,.project .logo.stockr{border-radius:0px;-webkit-filter:drop-shadow(0px 0px 15px rgba(0,0,0,0.3));filter:drop-shadow(0px 0px 15px rgba(0,0,0,0.3));width:150px}.project .description{line-height:1.6em;margin:24px 6px;text-align:justify;text-align-last:center}.project .source{margin:12px;font-family:-apple-system, BlinkMacSystemFont, sans-serif}.project .tags{padding:6px;display:inline-flex;flex-flow:row wrap;justify-content:center}.project .tags .tag{font-size:14px;border-radius:4px;border:1px solid rgba(255,255,255,0.5);padding:3px 9px;margin:2px;cursor:default;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.project .tags .tag.time,.project .tags .tag.team-size{color:rgba(255,255,255,0.7);background-color:rgba(255,255,255,0.1);font-family:-apple-system, BlinkMacSystemFont, sans-serif;text-transform:uppercase}.project .tags .tag.tech{color:rgba(255,255,255,0.7)}.project .gallery{margin:12px 0}.project .gallery .pswp-thumbnail{display:inline-block;margin:3px}.project .gallery .pswp-thumbnail img{height:12vmin;width:auto;border-radius:9px;cursor:-webkit-zoom-in;cursor:zoom-in;-webkit-transition:all 0.2s ease-out;transition:all 0.2s ease-out;border:3px solid transparent;box-shadow:0px 0px 21px rgba(0,0,0,0.1)}.project .gallery .pswp-thumbnail img:hover{-webkit-transform:scale(1.3) translateY(-6px);transform:scale(1.3) translateY(-6px);box-shadow:0px 0px 36px 21px rgba(0,0,0,0.2);border:3px solid #0b65ff}.project .gallery .pswp-thumbnail img:active{-webkit-filter:brightness(0.7);filter:brightness(0.7)}.project .gallery .pswp__caption,.project .gallery .pswp__counter{font-family:-apple-system, BlinkMacSystemFont, sans-serif}.project .gallery .pswp__caption__center{text-align:center;font-size:15px}.project .gallery .pswp__button--share{display:none}.project .gallery .pswp__bg{opacity:0.9 !important}.project .gallery .pswp__img{border-radius:12px;box-shadow:0px 0px 21px 21px rgba(0,0,0,0.6)}

.about .about-header{font-size:6vmax;letter-spacing:15px;position:absolute;text-align:center;top:15vmin;left:0;right:0;margin:auto;color:rgba(255,255,255,0.8);text-shadow:0px 0px 48px rgba(0,0,0,0.9);text-transform:uppercase}.about .about-details{padding:24px;background-color:#323d52;color:rgba(255,255,255,0.8);text-align:center}.about .about-details .separator{margin:24px 0;font-size:12px;color:rgba(255,255,255,0.3)}.about .about-details .about-picture{margin:15px auto;background-image:url(/static/media/mk.b9b45c0f.jpg);background-size:cover;width:72px;height:72px;box-shadow:0px 0px 24px 12px rgba(0,0,0,0.1);border-radius:50%}.about .about-details .about-title{text-transform:uppercase;color:#86e6cf;font-size:21px;margin:9px 0}.about .about-details .about-interests .about-interests-list{line-height:1.8em}.about .about-details .about-education .about-education-title,.about .about-details .about-education .about-experience-title,.about .about-details .about-experience .about-education-title,.about .about-details .about-experience .about-experience-title{color:#50c0a6;font-size:18px}.about .about-details .about-education .about-education-info,.about .about-details .about-education .about-experience-info,.about .about-details .about-experience .about-education-info,.about .about-details .about-experience .about-experience-info{margin:9px 0;color:rgba(255,255,255,0.6);line-height:1.8em;font-size:15px}.about .about-details .about-education .about-education-info .about-experience-desc,.about .about-details .about-education .about-experience-info .about-experience-desc,.about .about-details .about-experience .about-education-info .about-experience-desc,.about .about-details .about-experience .about-experience-info .about-experience-desc{color:rgba(255,255,255,0.7);font-style:oblique;margin-bottom:9px}.about .about-details .about-education .about-education-info .tag,.about .about-details .about-education .about-experience-info .tag,.about .about-details .about-experience .about-education-info .tag,.about .about-details .about-experience .about-experience-info .tag{display:inline-flex;font-size:12px;border-radius:6px;line-height:1em;border:1px solid rgba(255,255,255,0.3);padding:3px 6px;cursor:default;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.about .about-details .about-education .about-education-info .tag.time,.about .about-details .about-education .about-experience-info .tag.time,.about .about-details .about-experience .about-education-info .tag.time,.about .about-details .about-experience .about-experience-info .tag.time{color:rgba(255,255,255,0.7);background-color:rgba(255,255,255,0.1);text-transform:uppercase}.about .about-details .about-links{margin:12px 6px;display:flex;align-items:center;justify-content:center}.about .about-details .about-links .link-btn{margin:3px;padding:3px 6px;width:42px;color:white;background:none;font-size:36px;border-radius:6px;text-decoration:none;-webkit-transition:0.6s all ease-out;transition:0.6s all ease-out}.about .about-details .about-links .link-btn.linkedin:hover{background-color:#0077b5}.about .about-details .about-links .link-btn.github:hover{background-color:#6e5494}.about .about-details .about-links .link-btn.twitter:hover{background-color:#1da1f2}.about .about-details .about-links .link-btn.instagram:hover{background-color:#c13584}.about .about-details .about-links .link-btn.resume:hover{background-color:#00a98f;cursor:alias}.about .about-details .about-links .link-btn.email:hover{background-color:#0f93ff;cursor:ne-resize}

.footer{text-align:center;padding:6px;color:#7a7a7a;font-size:12px}.footer .footer-react,.footer .footer-react>a{color:#575757}

